<script>
export default {
  name: 'mainIndex',
  components: {},
  mounted () {
    this.selfAdaption()
  },
  methods: {
    // echart
    selfAdaption () {
      let that = this
      setTimeout(() => {
        window.onresize = function () {
          if (that.$refs.echarts) {
            that.$refs.echarts.chart.resize()
          }
        }
      }, 10)
    }
  }
}
